.nav-flex {
  background: #ffb280;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  padding: 0 2rem; }
  .nav-flex .brand-name {
    font-size: 1.5rem;
    letter-spacing: 1.3px;
    font-weight: bold; }
  .nav-flex .subtitle {
    font-size: 1rem;
    opacity: 0.7;
    font-weight: normal;
    font-style: italic; }

.hamburger-menu {
  width: 2rem;
  cursor: pointer;
  display: inline-block;
  margin-left: auto;
  transition: all 500ms ease-in-out; }
  .hamburger-menu .bar {
    background: #fff;
    width: 100%;
    height: 3px;
    margin: 6px 0;
    border-radius: 50px;
    transition: all 500ms ease-in-out; }

#about {
  background: #fff; }

.container {
  display: flex;
  flex-direction: column;
  padding: 0 2rem;
  margin: 2rem auto;
  align-items: center; }
  .container .profile-pic {
    width: 250px;
    height: 250px;
    border-radius: 25px; }
  .container .personal-desc {
    letter-spacing: 1.2px;
    margin-top: 2rem;
    line-height: 1.3rem;
    text-align: justify;
    hyphens: auto; }

#study {
  background: #ffe6e6; }
  #study .section-title {
    text-transform: uppercase;
    letter-spacing: 1.3px; }

/* Timeline */
.timeline {
  border-left: 5px solid #ffb280;
  border-bottom-right-radius: 4px;
  border-top-right-radius: 4px;
  color: #333;
  margin: 2rem auto;
  letter-spacing: 0.7px;
  position: relative;
  line-height: 1.4em;
  padding: 50px;
  max-width: 50%; }
  .timeline .event {
    padding-bottom: 50px;
    position: relative; }
    .timeline .event:last-of-type {
      padding-bottom: 0;
      margin-bottom: 0;
      border: none; }
    .timeline .event::before, .timeline .event::after {
      position: absolute;
      display: block;
      top: 0; }
    .timeline .event::before {
      padding-left: 2rem;
      left: -217.5px;
      color: #333;
      content: attr(data-date);
      text-align: right;
      min-width: 120px;
      opacity: 0.7; }
    .timeline .event:after {
      box-shadow: 0 0 0 3px #ffb280;
      left: -57.85px;
      background: #fff;
      border-radius: 50%;
      height: 11px;
      width: 11px;
      content: '';
      top: 5px; }
  .timeline .event-info {
    text-align: justify;
    hyphens: auto; }

#therapy .container {
  display: flex;
  flex-direction: column; }
  #therapy .container .section-title {
    text-transform: uppercase;
    letter-spacing: 1.3px;
    margin: 0 auto; }
  #therapy .container .divider {
    width: 50%;
    height: 3px;
    background: #ffb280;
    margin: auto;
    border-radius: 50px; }

.card {
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  text-align: justify;
  hyphens: auto; }
  .card .card-title {
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 1.3px; }
  .card .card-info {
    text-align: justify;
    hyphens: auto;
    line-height: 1.3rem;
    letter-spacing: 1.05px; }
  .card .card-link {
    padding-left: 5px;
    color: #c587df;
    transition: all 500ms ease-in-out; }
    .card .card-link:hover {
      color: #7f2ba2; }

#contact {
  background: #ffe6e6;
  padding: 2rem;
  color: #333; }
  #contact .section-title {
    text-transform: uppercase;
    letter-spacing: 1.3px;
    text-align: center; }
  #contact .form {
    display: flex;
    flex-direction: column; }
    #contact .form label {
      padding: 20px 0 5px 0;
      font-weight: 700;
      letter-spacing: 1.2px;
      text-transform: uppercase;
      font-size: 0.9rem; }
    #contact .form input,
    #contact .form textarea {
      padding: 7px 3px;
      letter-spacing: 1px; }
      #contact .form input:focus,
      #contact .form textarea:focus {
        outline: 2px solid #ffb280; }
      #contact .form input::placeholder,
      #contact .form textarea::placeholder {
        opacity: 0.7; }
    #contact .form .submit-btn {
      width: 220px;
      height: 40px;
      background: #c587df;
      cursor: pointer;
      border: 1px solid #c587df;
      text-transform: uppercase;
      border-radius: 5px;
      color: #fff;
      margin: 1rem auto;
      transition: all 500ms ease-in-out; }
      #contact .form .submit-btn:hover {
        background: #7f2ba2;
        transform: translateY(-2px); }

.footer {
  background: #ffb280;
  padding: 2rem;
  text-align: center;
  color: #fff; }
  .footer .footer-content .dev-logo {
    width: 120px;
    vertical-align: middle;
    padding-left: 5px;
    transition: all 500ms ease-in-out; }
    .footer .footer-content .dev-logo:hover {
      transform: scale(1.05); }
