.card {
  display: flex;
  flex-direction: column;
  margin-top: $gutterWidth;
  @include justifyText;

  .card-title {
    text-transform: uppercase;
    text-align: center;
    letter-spacing: 1.3px; }

  .card-info {
    @include justifyText;
    line-height: 1.3rem;
    letter-spacing: 1.05px; }

  .card-link {
    padding-left: 5px;
    color: $accentColor;
    @include transition;
    &:hover {
      color: darken($accentColor, 30); } } }
