#about {
  background: $lightColor; }

.container {
  display: flex;
  flex-direction: column;
  padding: 0 $gutterWidth;
  margin: $gutterWidth auto;
  align-items: center;

  .profile-pic {
    width: 250px;
    height: 250px;
    border-radius: 25px; }

  .personal-desc {
    letter-spacing: 1.2px;
    margin-top: $gutterWidth;
    line-height: 1.3rem;
    @include justifyText; } }
